
.edit-panel-container{
	display: none
}
.edit-panel-mask{
	position:fixed;
	top:0;left:0;
	width:100%;height:100%;
	background-color:rgba(0,0,0,.3);
	z-index:99;
}
.edit-panel{
	position:absolute;
	top:20%;left:38%;
	/*transform: translate(-50%,-50%);*/
	width:450px;height:450px;
	background-color: rgb(255,255,255);
	border-radius: 5px;
	z-index:100;
}
.edit-panel-meeting{
	height:510px;
}
.edit-panel form{
	margin:45px auto;
	width:70%;
}
.edit-panel button{
	margin-top:30px;
}
.edit-panel .quit{
	width:32px;height:32px;
	margin:10px;
	cursor: pointer;
}



