
.content-nav{
        font-size: 16px;
        margin-bottom: 30px;
}
.content-nav ul{
    	padding:0;
        border-bottom: 1px solid rgb(210,210,210);
}
.content-nav li{
        display: inline-block;
        width:100px;height:50px;
        line-height: 50px;
        text-align: center;
        cursor:pointer;
}
.content-nav li.active{
        color:rgb(0,197,195);
        border-bottom:2px solid rgb(0,197,195);
}
.content-nav li:hover{
        color:rgb(0,197,195);
}



